<template>
	<view class="loginContainer">
		仅为实例
		<block v-if="!isLogin"><mirrorego-login :loginCode="loginCode"></mirrorego-login></block>
		<block v-else>已登录</block>
	</view>
</template>

<script>
export default {
	data() {
		return {
			loginCode: ''
		};
	},
	onShow() {
		uni.login({
			provider: 'weixin',
			success: loginRes => {
				this.loginCode = loginRes.code;
			}
		});
	},
	methods: {
		authorized() {}
	}
};
</script>

